@mixin star-rating($baseColor: #000, $activeColor: gold, $ratingStarWidth: 10px, $ratingStarGap: 5px, $icomoon: false) {
//      
// $ratingStarWidth -      (    px!!)
// $ratingStarGap -  /  (    px!!)
// $icomoon -    icomoon       svg-.    : '\e900'
	//   icomoon -    false
	$kef: 1.4;
  
  display: flex;
  width: ($ratingStarWidth + $ratingStarGap) * 5;
  margin-left: -$ratingStarGap / 2 ;
  margin-right: -$ratingStarGap / 2  ;
  font-size: $ratingStarWidth;
  
  &__star{
    flex: 0 0 calc(20% - #{$ratingStarGap});
    margin: 0 $ratingStarGap / 2;
    height: $ratingStarWidth;
    display: block;
    position: relative;
    
    &:before{
      display: block;
      color: $baseColor;
      // position: absolute;
      @if $icomoon != false { 
      		content: $icomoon;
      		font-family: 'rating' !important;
			    speak: never;
			    font-style: normal;
			    font-weight: normal;
			    font-variant: normal;
			    text-transform: none;
			    -webkit-font-smoothing: antialiased;
			    -moz-osx-font-smoothing: grayscale;

  		}@else{
      		content: '';

  		}
      line-height: $ratingStarWidth;
      // left: 50%;
      // transform: translateX(-50%);
    }
    &.active{
      &:before{
        color: $activeColor;
        //        
     //  	color: transparent;
     //  	background-image: url(https://lh5.googleusercontent.com/-DZ5jyR2NRfk/AAAAAAAAAAI/AAAAAAAAACk/I8BNLS9vfCs/photo.jpg?sz=50);
	    // background-size: contain;
	    // background-repeat: no-repeat;
      }
    }
  }
}
@mixin simple-rating($baseColor: #000, $activeColor: gold, $ratingStarWidth: 20px, $ratingStarGap: 5px, $icomoon: false) {
	//       ,   
	// $ratingStarWidth -      (    px!!)
	// $ratingStarGap -  /  (    px!!)
	// $icomoon -    icomoon       svg-.    : '\e900'
	$kef: 1.4;
	display: flex;
	width: ($ratingStarWidth + $ratingStarGap) * 5;
	margin-left: -$ratingStarGap /2;
	margin-right: -$ratingStarGap /2;
	flex-direction: row-reverse;
	font-size: $ratingStarWidth * $kef;
	label{
		width: calc(20% - #{$ratingStarGap});
		// flex: 0 0 calc(20% - #{$ratingStarGap});// 
		margin: 0 $ratingStarGap / 2;
		height: $ratingStarWidth;
		display: block;
		position: relative;
		&:before{
			display: block;
			width: 100%;
			// margin: 0 -7px;
			position: absolute;
			@if $icomoon != false { 
      		content: $icomoon;
      		font-family: 'rating' !important;
			    speak: never;
			    font-style: normal;
			    font-weight: normal;
			    font-variant: normal;
			    text-transform: none;
			    -webkit-font-smoothing: antialiased;
			    -moz-osx-font-smoothing: grayscale;

  		}@else{
      		content: '';
  		}
			line-height: $ratingStarWidth;
			left: 50%;
			transform: translateX(-50%);
			color: $baseColor;
		}
		&:hover{
			cursor: pointer;
			&:before{
				color: $activeColor;
			}
			& ~ label:before{
				color: $activeColor;
				//        
				     // color: transparent;
				     // background-image: url(https://lh5.googleusercontent.com/-DZ5jyR2NRfk/AAAAAAAAAAI/AAAAAAAAACk/I8BNLS9vfCs/photo.jpg?sz=50);
				    // background-size: contain;
				    // background-repeat: no-repeat;
			}
		}

	}
	input{
		position: absolute;
		left: -9999px;

		&:checked ~ label{
			&:before{
				color: $activeColor; 
				//        
			     	//color: transparent;
			     	// background-image: url(https://lh5.googleusercontent.com/-DZ5jyR2NRfk/AAAAAAAAAAI/AAAAAAAAACk/I8BNLS9vfCs/photo.jpg?sz=50);
				    // background-size: contain;
				    // background-repeat: no-repeat;
			}
		}
	}

}

@mixin crystal-rating($baseColor: #000, $activeColor: gold, $fontSize: 40, $spacing: 0,  $icomoon: false) {
	// $spacing   'px'
	// $icomoon -    icomoon       svg-.    : '\e900'
	  display: flex;
	  align-items: center;
	  display: flex;
	  position: relative;
	  font-size: $fontSize + px;
	  @if $icomoon != false { 
	  	line-height: 1;

  	}@else{
	  	line-height: 0.75;
  	}
	  letter-spacing: $spacing + px;
	  // outline: 1px solid pink;
	  
	  &__body{
	  	position: relative;
	    // width: ($fontSize * 5 * 0.833) + $spacing * 5 * 0.833 + px;
	    // outline: 1px solid;
	    &:before{
	    	display: block;
	    	@if $icomoon != false { 
	    		content: $icomoon $icomoon $icomoon $icomoon $icomoon;
	    		font-family: 'rating' !important;
	    		speak: never;
	    		font-style: normal;
	    		font-weight: normal;
	    		font-variant: normal;
	    		text-transform: none;
	    		-webkit-font-smoothing: antialiased;
	    		-moz-osx-font-smoothing: grayscale;
	    		color: $baseColor;

	    	}@else{
	    		content: '';
	    	}
	    }
	  }
	  &__items{
	    position: absolute;
	    display: flex;
	    width: 100%;
	    height: 100%;
	    top: 0;
	    left: 0;
	    
	    input{
	      flex: 0 0 20%;
	      display: block;
	      height: 100%;
	      opacity: 0;
	      margin: 0;
	    }
	  }
		.crystal-rating__active{
		  position: absolute;
		  width: 0;
		  display: block;
		  height: 100%;
		  top: 0;
		  left: 0;
		  transition: width 0.2s ease-in;
		 
		  &:before{
		  	@if $icomoon != false { 
	    		content: $icomoon $icomoon $icomoon $icomoon $icomoon;
	    		font-family: 'rating' !important;
	    		speak: never;
	    		font-style: normal;
	    		font-weight: normal;
	    		font-variant: normal;
	    		text-transform: none;
	    		-webkit-font-smoothing: antialiased;
	    		-moz-osx-font-smoothing: grayscale;

	    	}@else{
	    		content: '';
	    	}
		    position: absolute;
		    display: block;
		    height: 100%;
		    width: 100%;
		    top: 0;
		    left: 0;
		    color: $activeColor;
		    overflow: hidden;
		  }
		}
		.crystal-rating__value{
		  display: inline-block;
		  font-size: .7em;
		  margin-left: 15px;
		  letter-spacing: normal;
		}
}
